import React from "react"
import { useStaticQuery, graphql } from "gatsby"

import Link from "../components/helpers/link"

export default () => {
  const data = useStaticQuery(
    graphql`
      query {
        noticesToml {
          notices {
            name
            url
          }
        }
      }
    `
  )
  const noticeData = data.noticesToml.notices
  const noticeEntries = []

  for (const [index, notice] of noticeData.entries()) {
    noticeEntries.push(
      <div className="col-4 p-2" key={"notice-"+index}>
        <a href={notice.url}>{notice.name}</a>
      </div>
    )
  }

  return (
  <section className="bg-light-grey has-divider">
    <div className="container pt-6">
      <div className="row pb-0">
        <div className="col-xl-8 col-lg-9">
          <h2 className="display-4">Open Source Notices</h2>
          <p className="display-6">
            Notice required by licensors of any open source libraries or components related to any of the applicable services. For questions please contact <Link to="mailto:opensource@salesforce.com">opensource@salesforce.com</Link>
          </p>
        </div>
      </div>
      <div className="row">
        { noticeEntries }
      </div>
    </div>
  </section>
)}